.comment-content {
	padding: 0;
}

.comments-area {
	font-size: 17px;
	line-height: 1.7;
	margin-left: auto;
	margin-right: auto;
	max-width: 700px;
	padding: 0 4%;
	@media only screen and (min-width: 768px) {
	    padding: 0;
	}
}

.no-comments {
	text-align: center;
}

.comments-area--wrapper {
	background: #fafafa;
	padding-top: 70px;
	padding-bottom: 45px;
	margin: 80px -4% 0;
	width: calc(100% + 8%);

	@media only screen and (min-width: 768px) {
	    margin: 5vw -4% 0;
	    padding-top: 80px;
	}
}

.comment-respond p, 
.comment-respond input {
	margin-bottom: 0;
}

.comments {
	margin-bottom: 40px;
	z-index: 999;

	ol,
	ul,
	li {
		list-style-type: none;
		margin: 0;
		padding: 0;
	}

	&-title {
		margin-bottom: 30px;
		margin-top: 0;
		text-align: center;
	}
}

.comment {
	margin: 0 0 30px !important;

	.comment-respond {
		margin-top: 30px;
	}
	
	.avatar {
		border-radius: 100px;
		float: left;
		width: 30px;
		height: 30px;
		margin: 8px 10px 0 0 !important;

		@media only screen and (min-width: 514px) {
			width: 36px;
			height: 36px;
			margin: 7px 14px 0 0 !important;
		}
	}

	&-meta,
	&-author {
		color: rgba(0, 0, 0, 0.5);
		display: block;
		font-size: 13px;
		margin: 0 0 10px;

		@media only screen and (min-width: 514px) {
			font-size: 16px; 
		}

		a {
			color: rgba(0, 0, 0, 0.5);

			&:hover {
				color: $black;
			}
		}

		b {
			font-weight: normal;
			-moz-osx-font-smoothing: inherit;
			-webkit-font-smoothing: inherit;
		}
	}

	&-author {
		margin-bottom: -2px;

		span, cite, a {
			color: rgba(0, 0, 0, 0.5);
			font-size: 13px;
			display: inline-block;
			font-style: normal;
			font-weight: normal;
			@media only screen and (min-width: 514px) {
				font-size: 16px; 
			}
		}
	}


	&-notes {
		display: none;
	}

	&-content {
		margin-left: 50px;
	}

	&-content a {
		word-wrap: break-word;
	}

	&-body {
		clear: both;

		@media only screen and (min-width: 514px) {
			margin: 0 0 0 50px;      
		}

		.pingback & {
			margin-left: 0;
		}

		p {
			margin-bottom: 15px;
		}
	}
}

.children {
	margin: 0;
	padding: 0;

	@media only screen and (min-width: 600px) {
		.comments & {
			margin: 0 0 0 50px;
		}
	}
}

.pinglist {
	float: left;
}

.bypostauthor {
	display: block;
}

.comment-form {
	margin-bottom: -110px;
	overflow: hidden;
	transition: margin 200ms cubic-bezier(0.75, 0, 0.25, 1) 50ms;

	@media only screen and (min-width: 769px) {
		margin-bottom: -55px;
	}

	& label {
		@include hide();
	}

	&.js--focus {
		margin-bottom: 70px;
		transition: margin 200ms cubic-bezier(0.75, 0, 0.25, 1) 50ms; 
	}

	p {
		margin-bottom: 10px;
		position: relative;
	}

	.comment-form-url {
		display: none;
	}
}

.comment .comment-form {
	@media only screen and (min-width: 769px) {
		margin: 0 0 0 50px;
	}
}

.is-customize-preview .comment-form,
.admin-bar .comment-form {
	margin-bottom: -10px;

	&.js--focus {
		margin-bottom: 50px;
	}
}


body:not(.single-product) .comment-form-comment, body:not(.single-product) .comment-form-author, body:not(.single-product) .comment-form-email {

	textarea {
		font-size: 17px;
		margin: 0;
		max-height: 180px;
		line-height: 1.7 !important;
	}
}

.form-submit {
	text-align: left;
	margin-bottom: 0 !important;
	clear: left;

	@media (max-width: 768px) {
		& input {
			width: 100%;
		}
	}
}


@media only screen and (min-width: 769px) {
	.comment-form-author, .comment-form-email {
		float: left;
		display: inline-block;
		width: calc( 50% - 5px);
	}

	.comment-form-author {
		margin-right: 5px;
	}

	.comment-form-email {
		margin-left: 5px;
	}
}

.comment.depth-3,
.comment.depth-4,
.comment.depth-5,
.comment.depth-6,
.comment.depth-7 {

	p {
		width: 100%;
		margin-right: 0;
		margin-left: 0;
	}
}

#respond {
    overflow: hidden;
	@include clearfix;

	.form-submit, .comment-form-author, .comment-form-email {
		opacity: 0;
		transition: opacity 200ms cubic-bezier(0.75, 0, 0.25, 1) 0ms;

		&.js--focus {
			opacity: 1;
			transition: opacity 200ms cubic-bezier(0.75, 0, 0.25, 1) 100ms;
		}
	}

	.comment & {
		.form-submit, .comment-form-author, .comment-form-email {
			opacity: 1;
		}
	}
}